.App {
  text-align: center;
}
* {
  margin: 0px;
  padding: 0px;
}
.todoItem {
  display: flex;
  justify-content: space-between;
  align-items: start;
  background-color: #002884;
  color: white;
  padding: 10px;
  margin: 5px 10px;
  text-align: left;
  border-radius: 3px;
}
.todoItem h3 {
  letter-spacing: 0.5px;
  text-transform: capitalize;
  font-weight: 700;
}
.todoItem p {
  text-transform: none;
  transition: 0.3s;
}
.todoItem:hover {
  & p {
    padding: 10px 5px;
  }
}
